import React from 'react';
import { string, shape } from 'react-proptypes';

import './ExtraBlock.scss';

const ExtraBlock = ({ params }) => (
    <div className="extra-block__container">
        <h2>
            {params.title}
        </h2>

        <p className="extra-block__description subline2">
            {params.description}
        </p>

        <p className="extra-block__action button-text-large">
            {params.action}
        </p>

    </div>
);

ExtraBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       action: string,
    })
};

export default ExtraBlock;



