import React from 'react';
import { string, shape } from 'react-proptypes';

import { Button, Input as ShInput } from '../../../ui';

import './MainBlock.scss';

const MainBlock = ({ params }) => (
    <div className="main-block__container">
        <h1>
            {params.title}
        </h1>

        <p className="main-block__description subline2">
            {params.description}
        </p>

        <form className="main-block__form"
            action={process.env.API_URL}
            method="post">

            <ShInput placeholder={params.inputPlaceholder} 
                name="email"
                type="email"/>

            <Button type="submit">
                {params.buttonText}
            </Button>
        </form>
    </div>
);

MainBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       inputPlaceholder: string,
       buttonText: string
    })
};

export default MainBlock;



