import React from 'react';

import { Features } from '../components/Home/Features';
import { MainBlock } from '../components/Home/MainBlock';
import { ExtraBlock } from '../components/Home/ExtraBlock';

export default ({ data }) => {
    const page = data.markdownRemark;
    const { title, features, mainBlock, extraBlock } = page.frontmatter;

    return (
        <div>
            <MainBlock params={mainBlock} />

            <Features items={features}/>

            <ExtraBlock params={extraBlock} />

            <div dangerouslySetInnerHTML={{ __html: page.html }} />
        </div>
    );
};

export const query = graphql`
    query HomePageQuery {
        markdownRemark(fields: { slug: { eq: "/" } }) {
            html
            frontmatter {
                title
                mainBlock {
                    title
                    description
                    buttonText
                    inputPlaceholder
                }
                extraBlock {
                    description
                    title
                    action
                }
                features {
                    title
                    color
                    icon
                    text
                }
            }
        }
    }
`;



