import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import './Features.scss';

const Feature = ({ title, icon, color, text }, index) => (
    <div className={`${color}-text col-sm-6 col-xs-12 col-lg-4 col-md-4`}
        key={index}>
        <div className="feature__container">
            <i className="material-icons">
                {icon}
            </i>

            <h3>
                {title}
            </h3>
            
            <p className="copytext-large">{text}</p>
        </div>
    </div>
);

const Features = ({ items }) => (
    <div className="container">
        <div className="features__container row">
            {
                items.map((item, index) => Feature(item, index))
            }
        </div>
    </div>
);

Features.propTypes = {
    items: arrayOf(shape({
       title: string,
       icon: string,
       color: string,
       text: string 
    }))
};

Features.defaultProps = {
    items: []
};

export default Features;



